.LoadingTodo-container {
    width: 90%;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    margin-left: 10px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
    border-radius: 10px;
    padding: 12px 0;
  }
  
  .LoadingTodo-text {
    margin: 24px 0 24px 24px;
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
    color: grey
  }
  
  .LoadingTodo-completeIcon,
  .LoadingTodo-deleteIcon {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 30px;
    width: 30px;
  }

  .LoadingTodo-completeIcon {
    position: absolute;
    left: 12px;
    border-radius: 5px;
  }
  .LoadingTodo-deleteIcon {
    border-radius: 50px;
    position: absolute;
    top: -15px;
    right: -10px;
  }
  
  .CgSandClock{
    width: 100%;
    color: grey ;
    font-size: 30px;
    display: flex;
    justify-content: center;
  }
  
  .LoadingTodo-container,
  .LoadingTodo-completeIcon,
  .LoadingTodo-deleteIcon {
    background: linear-gradient(90deg, rgba(250,250,250,1), rgb(200, 199, 199));
    background-size: 400% 400%;
    animation: loadingAnimation 3s ease-in-out infinite;
  }
  
  @keyframes loadingAnimation {
    0% {
      background-position: 0% 50%;
    }
    50% {
      background-position: 100% 50%;
    }
    100% {
      background-position: 0% 50%;
    }
  }